import React, { Component } from "react";
import './index.scss'

export class ModalWindows extends Component {
  static defaultProps = {
    dismiss: null,
    show: null
  };
  constructor(props) {
    super(props);
  }
  getElementConfig = (props) => {
    const res = {};
    const media = {...props.media, ...props.config, "key-id":props.id};
    for (const key in media) {
      if(media[key]){
        res[key] = media[key]
      }
    }
    return res
  }

  render() {
    const { props, state } = this;
    
    return (
      <section style={{width:'100%'}} className={`fox_modal_window ${props.className}`} {...this.getElementConfig(props)}>
        {props.children}
      </section>
    );
  }
}
