export const WhatsappTheme  = (attr) => {
  const el = document.querySelector(`[key-id="${attr.id}"]`)
  const key = el?.getAttribute?.("fox-message");
  // const message = 
  const message = {
    "diamond": THEME_DIAMOND,
    "gold": THEME_GOLD,
    "platinum": THEME_PLATINUM,
    "silver": THEME_SILVER,
    "buat-undangan": THEME_CREATE
  }

  if(message[key]){
    return message[key]
  }

  const redirectLink = el?.getAttribute?.("href");
  return `
  Hi kak Retna & Ulamulem team. 

  Apakah saya bisa request tema ini ${redirectLink} ? kalo boleh tahu berapa harganya yah
  
  Terima kasih banyak
  `
  
}