import React, { Component, createElement } from "react";
import './index.scss'
import { GenerateAtribute } from "../../@utils/generate-atribute";

const BLACK_LIST_EL_FOR_CHILD = {
  "iframe": true,
  "video": true,
  "img": true ,
  "br": true,
  "input": true,
  "textarea": true,
  "source": true
}


export class HtmlElement extends Component {
  constructor(props) {
    super(props);
  }
  
  getElementConfig = (props) => {
    const res = GenerateAtribute(props)
    if(res["fox-onclick"]){
      res["onClick"] = this.onClick.bind(this)
    }
    return res
  }
  generateParams = (props)=> {
    const media = {...props.media, ...props.config, "key-id":props.id};
    for (let key in media) {
      if(this.elRef){
        this.elRef.setAttribute(key, media[key])
      }
    }
  }

  onClick = () => {
    this.props.onClickTrigger?.(this.props)
  }

  componentDidMount = () => {
    if(this.props.className?.match(/triggerdone/)){
      this.props.onTriggerDone?.(this.props)
    }
    // this.generateParams(this.props)
  }

  render() {
    const { props, state } = this;
    if(BLACK_LIST_EL_FOR_CHILD[props.type]){
      return createElement(
        props.type,
        { 
          ...this.getElementConfig(props),
          ref: ref => this.elRef = ref
        },
        props.children,
      );
    }
    return createElement(
      props.type,
      { 
        ...this.getElementConfig(props),
        ref: ref => this.elRef = ref
      },
      props.media.text,
      props.children,
    );
  }
}
