import React, { Component, useEffect, useRef } from "react";
import PhotoAlbum from "react-photo-album";
import './index.scss'
import { GenerateAtribute } from "../../@utils/generate-atribute";
import { ServerRequest } from "../../@utils/utils-server-request";
import { BoxLoader } from "../../@component/loader/box-loader";

const RenderListItem = (props) => {
  const ref = useRef(null);

  const rerdenderChildren = () => {
    if(ref.current){
      const srcEl = ref.current.querySelector("[fox-data=src]"); 
      const titleEl = ref.current.querySelector("[fox-data=title]"); 
      const linkEl = ref.current.querySelector("[fox-data=link]"); 
      const waUrlEl = ref.current.querySelector("[fox-data=url]"); 
      // const lableEl = ref.current.querySelector("[fox-data='label']");
      if(srcEl){
        srcEl.setAttribute("src", props.item.src)
      } 
      if(titleEl){
        titleEl.innerHTML = props.item.title
      } 
      if(linkEl){
        linkEl.setAttribute("href", props.item.link)
      } 
      if(waUrlEl){
        waUrlEl.setAttribute("fox-message", props.item.link)
      } 
      
    }
  }
  useEffect(() => {
    rerdenderChildren()
  }, [])
  return(
    <div className="render-item" ref = {ref}>
      {props.children}
    </div>
  )
}

export class Product extends Component {
  static defaultProps = {
    dismiss: null,
    show: null
  };
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          src: "./images/theme/rina-dan-yudha.png",
          title: "Rina & Yudha",
          label: "PESAN",
          link: "https://rina-dan-yudha.ulamulem.com/"
        }
      ]
    }
  }
  getElementConfig = (props) => {
    const res = GenerateAtribute(props)
    return res
  }

  _timer = null;
  componentDidMount = () => {
    clearTimeout(this._timer)
    this._timer = setTimeout(this.getData)
  }
  getData = async ( ) => {
    this.setState({isFound: false, isFetching: true, isError: false, data: []}, () => {
      new ServerRequest().get({url: '/api/data.json'},(e) => {
        if(e.isFound){
          this.setState({
            ...e,
            data: e.data?.data || []
          })
        }else if(e.isError){
          this.setState({
            ...e,
            data: this.state.data
          })
        }
      })
    })
  }

  render() {
    const { props, state } = this;
    const photos = props.galery || [];
    return (
      <section style={{width:'100%'}} className={`fox-product ${props.className}`} {...this.getElementConfig(props)}>
        <BoxLoader isShow = {state.isFetching} />
        { !state.isFetching &&  state.data.map((item, i) => (
          <RenderListItem item = {item} key = {i}>
            {props.children}
          </RenderListItem>
        ))}
      </section>
    );
  }
}
