import { useCallback } from "react";
import  './index.scss'
import { OwnIcon } from "../../@component/icons/module-icons";

export const IconEl = (props) => {
    const getElementConfig = (props) => {
      const res = {};
      const media = {...props.media, ...props.config, "key-id":props.id};
      for (const key in media) {
        if(media[key]){
          res[key] = media[key]
        }
      }
      return res
    }

    return (
        <OwnIcon className="icon_custom" type={props.media["fox-value"] || "user"} {...getElementConfig(props)} />
    );
};